<template>
  <div id="subscription-app">
    <Loading v-if="loading" />
    {{message}}
    <BookSpecs 
       ref="bookspecs"
       v-bind:maximum_price="subscription[0].maximum_price"
       v-bind:minimum_goodreads_rating="subscription[0].minimum_goodreads_rating"
       v-bind:worst_allowable_condition="subscription[0].worst_allowable_condition"
       v-bind:minimum_page_length="subscription[0].minimum_page_length"
       v-bind:maximum_page_length="subscription[0].maximum_page_length"
       v-bind:series_handler="subscription[0].series_handler"
       v-bind:decision_method="subscription[0].decision_method"
       v-bind:genres="subscription[0].genres"
       v-bind:frequency="subscription[0].frequency"
       v-bind:minimum_number_goodreads_r="subscription[0].minimum_number_goodreads_r"
       v-bind:genre_lookup="genres[0]"
    />
    <Billing
       ref="billing"
       v-bind:last4="subscription[0].stripe_customer.last4"
       v-bind:zip="subscription[0].stripe_customer.zip"
       v-on:send-token="setToken"
    />
    <Shipping
       ref="shipping"
       v-bind:full_name="subscription[0].full_name"
       v-bind:address_line_1="subscription[0].address_line_1"
       v-bind:address_line_2="subscription[0].address_line_2"
       v-bind:city="subscription[0].city"
       v-bind:state_province_region="subscription[0].state_province_region"
       v-bind:zip="subscription[0].zip"
       v-bind:country_region="subscription[0].country_region"
    />
    <button @click="submitAll">Submit</button>
  </div>
</template>

<script>
import BookSpecs from './components/BookSpecs.vue'
import Billing from './components/Billing.vue'
import Shipping from './components/Shipping.vue'
import Loading from './components/Loading.vue'

if (!Drupal) {
var Drupal = {};
Drupal.settings = {};
Drupal.settings.obsequens = {
              maximum_price: 15,
              minimum_goodreads_rating: '',
              worst_allowable_condition: 'acceptable',
              minimum_page_length: '',
              maximum_page_length: '',
              series_handler: 'any',
              decision_method: [],
              genres: [],
              frequency: 'monthly',
              minimum_number_goodreads_r: 0,

              stripe_token: '',
              stripe_customer: {
                  last4: '',
                  zip: '',
              },

              full_name: '',
              address_line_1: '',
              address_line_2: '',
              city: '',
              state_province_region: '',
              zip: '',
              country_region: '',
          };
      }

var apiURL = 'https://obsequens.org/contentasjson/node/';

export default {
  name: 'subscription_app',
  components: {
    BookSpecs,
    Billing,
    Shipping,
    Loading,
  },
  data() {
      return {
          subscription: [{
              maximum_price: Drupal.settings.obsequens.maximum_price,
              minimum_goodreads_rating: Drupal.settings.obsequens.minimum_goodreads_rating,
              worst_allowable_condition: Drupal.settings.obsequens.worst_allowable_condition,
              minimum_page_length: Drupal.settings.obsequens.minimum_page_length,
              maximum_page_length: Drupal.settings.obsequens.maximum_page_length,
              series_handler: Drupal.settings.obsequens.series_handler,
              decision_method: Drupal.settings.obsequens.decision_method,
              genres: Drupal.settings.obsequens.genres,
              frequency: Drupal.settings.obsequens.frequency,
              minimum_number_goodreads_r: Drupal.settings.obsequens.minimum_number_goodreads_r,

              stripe_token: '',
              stripe_customer: Drupal.settings.obsequens.stripe_customer,

              full_name: Drupal.settings.obsequens.full_name,
              address_line_1: Drupal.settings.obsequens.address_line_1,
              address_line_2: Drupal.settings.obsequens.address_line_2,
              city: Drupal.settings.obsequens.city,
              state_province_region: Drupal.settings.obsequens.state_province_region,
              zip: Drupal.settings.obsequens.zip,
              country_region: Drupal.settings.obsequens.country_region,
          }],
          loading: false,
          genres: {},
          message: '',
          sub_id: '',

          new_sub: false,
      }
  },
  mounted: function() {
      this.getGenres();
  },
  methods: {
      getGenres: function() {
          this.$http.get('https://obsequens.org/api/genres.json').then(function(data) {
              this.$set(this.genres, 0, data.body);
          });
      },
      getSubscription: function(sub_id) {
          this.loading = true;
          this.sub_id = sub_id;
          this.$http.get(apiURL+sub_id).then(function(subscription) {
              this.loading = false;
              this.$set(this.subscription, 0, subscription.body);
          });
      },
      submitAll: function() {
          this.message = '';
          // send card information to Stripe
          this.$refs.billing.createCardToken();
          // at this point the baton is handed off to Billing and the
          // submitting process continues in setToken
      },
      setToken: function(event) {
          if (!event) {
              this.message = "There were problems with your card";
              return;
          }
          if (event != ' ') {
              this.stripe_token = event.id;
          }
          var bookSpecs = this.$refs.bookspecs.sendData();
          console.log(bookSpecs);
          // verify max_price and decision method
          if (!bookSpecs.maximum_price) {
              this.message = "Maximum price is required";
              return;
          }
          if (!bookSpecs.decision_method.length) {
              this.message = "Decision method is required";
              return;
          }

          var shippingInfo = this.$refs.shipping.sendData();
          console.log(shippingInfo);

          // now send to drupal
          var data = {bookSpecs: bookSpecs, stripe_token: this.stripe_token, shippingInfo: shippingInfo};

//          var xhr = new XMLHttpRequest();
//          xhr.open("POST", 'https://obsequens.org/api/create_subscription', true);
//          xhr.setRequestHeader('Content-Type', 'application/json');
//          xhr.send(JSON.stringify({
//              data: data
//          }));

          this.$http.post('https://obsequens.org/api/create_subscription', {data:data}).then(function(data){console.log(data)});
      },
  },
}
</script>

<style>
#subscription-app .section {
    border: 2px solid black;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
    padding-top: 0;
}
#subscription-app .section h2 {
    background-color: black;
    color: white;
    margin-top: 0;
    margin-left: -10px;
    margin-right: -10px;
    padding-left: 10px;
}
</style>
