<template>
    <div class="section">
        <h2>Billing</h2>

        <div v-show="last4" style="position: relative;">
            Card: **** **** **** {{last4}}<br>
            ZIP: {{zip}}<br>
            <button @click="edit" style="position:absolute; top:0; right:0;">Change</button>
        </div>
        <div v-show="!last4" class="form-row">
            <label for='card-element'>Credit or debit card</label>
            <div id="card-element">
            </div>

            <div id="card-errors" role="alert"></div>
        </div>
    </div>
</template>

<script>
var stripe, card;

export default {
    name: 'Billing',
    props: [
        'last4', 'zip',
    ],
    data() {
        return {
            'editing': false,
        }
    },
    mounted() {
        this.mountStripe();
        if(!this.last4) {
            this.edit();
        }
    },
    methods: {
        edit: function() {
            this.editing = true;
            this.last4 = '';
        },
        mountStripe: function() {
            stripe = Stripe('pk_test_9lVYqtKN2QjWHJv0zxbKYUR4');
            var elements = stripe.elements();

            var style= {
                base: {
                    fontSize: '16px',
                    color: '#32325d',
                }
            };

            card = elements.create('card', {style: style});
            card.mount('#card-element');
            card.addEventListener('change', function(event) {
                var displayError = document.getElementById('card-errors');
                if (event.error) {
                    displayError.textContent = event.error.message;
                } else {
                    displayError.textContent = '';
                }
            });
        },
        createCardToken: function() {
            if (this.last4) {
                this.stripeTokenHandler(' ');
            } else {
                var ths = this;
                stripe.createToken(card).then(function(result) {
                    if (result.error) {
                        var errorElement = document.getElementById('card-errors');
                        errorElement.textContent = result.error.message;
                        ths.stripeTokenHandler('');
                    } else {
                        ths.stripeTokenHandler(result.token);
                    }
                });
            }
        },
        stripeTokenHandler: function(token) {
            this.$emit('send-token', token);
        },
    },
}
</script>

