<template>
  <div class="section">
      <h2>Book Specs</h2>

      <div>
          <label for="minimum_goodreads_rating">Minimum goodreads rating:</label>
          <input v-model="mutable_minimum_goodreads_rating" id="minimum_goodreads_rating" type="number" min="0" max="5"> stars
      </div>

      <div>
          <label for="minimum_number_goodreads_r">Minimum number of Goodreads ratings:</label>
          <input v-model="mutable_minimum_number_goodreads_r" id="minimum_number_goodreads_r" type="number"> ratings
      </div>

      <div>
          <label for="maximum_price">Maximum price:</label>
          $<input v-model="mutable_maximum_price" id="maximum_price" type="number" step="0.01">
      </div>

      <div>
          <label for="minimum_page_length">Page length range:</label>
          <div style="display:inline-block;">
              Between <input v-model="mutable_minimum_page_length" id="minimum_page_length" type="number"> and
              <input v-model="mutable_maximum_page_length" id="maximum_page_length" type="number"> pages
          </div>
      </div>

      <div>
          <label for="worst_allowable_condition">Worst allowable condition:</label>
          <select v-model="mutable_worst_allowable_condition" id="worst_allowable_condition">
              <option value="acceptable">Used - Acceptable</option>
              <option value="good">Used - Good</option>
              <option value="very_good">Used - Very Good</option>
              <option value="like_new">Used - Like New</option>
              <option value="new">New</option>
          </select>
      </div>

      <div>
          <label for="series_handler">How to handle books part of a series:</label>
          <select v-model="mutable_series_handler" id="series_handler">
              <option value="if_read">Only allow a sequel if the previous one is in my "read" list</option>
              <option value="no_series">Don't allow any books that are part of a series</option>
              <option value="any">Allow any book, regardless of series</option>
          </select>
      </div>

      <div>
          <label>Decision method:</label>
          <div id="decision_method">
              <input type="checkbox" id="newly_released"    value="newly_released"    v-model="mutable_decision_method"><label for="newly_released">Choose newly released books</label><br>
              <input type="checkbox" id="popular_this_week" value="popular_this_week" v-model="mutable_decision_method"><label for="popular_this_week">Popular this week</label><br>
              <input type="checkbox" id="popular"  value="popular"  v-model="mutable_decision_method"><label for="popular">Popular all time</label><br>
              <input type="checkbox" id="random"            value="random"            v-model="decision_method"><label for="random">Completely random</label>
          </div>
      </div>

      <div>
          <label for="frequency">Frequency of order:</label>
          <select v-model="mutable_frequency" id="frequency">
              <option value="weekly">Weekly</option>
              <option value="biweekly">Biweekly</option>
              <option value="monthly">Monthly</option>
              <option value="bimonthly">Bimonthly</option>
          </select>
      </div>

      <div id="genre-block">
          <h3>Genres</h3>
          <div id="genre-list">
              <ul>
                  <li v-for="(genre, index) in mutable_genres" v-bind:key="index">
                      {{genre_lookup[genre]}} <img src="http://www.inextenso-avocats.fr/wp-content/uploads/2017/06/false-2061131_960_720.png" class="remove-genre" @click="mutable_genres.splice(index, 1)">
                  </li>
              </ul>
          </div>
          <!--<input placeholder="Add a genre" v-model="new_genre" id="new_genre" @keyup.enter="addGenre">-->
          <AutocompleteInput
            placeholder="Add a genre"
            v-bind:items="Object.values(genre_lookup)"
            v-on:change="new_genre=$event"
            v-on:enter="addGenre"
          />
      </div>
  </div>
</template>

<script>
import AutocompleteInput from './AutocompleteInput.vue'

export default {
  name: 'BookSpecs',
  components: {AutocompleteInput},
  props: [
    'maximum_price',
    'minimum_goodreads_rating',
    'worst_allowable_condition',
    'minimum_page_length',
    'maximum_page_length',
    'series_handler',
    'decision_method',
    'genres',
    'frequency',
    'minimum_number_goodreads_r',
    'binding',

    'genre_lookup',
  ],
  data() {
      return {
          new_genre: '',

          mutable_genres: this.genres,

    mutable_maximum_price: this.maximum_price,
    mutable_minimum_goodreads_rating: this.minimum_goodreads_rating,
    mutable_worst_allowable_condition: this.worst_allowable_condition,
    mutable_minimum_page_length: this.minimum_page_length,
    mutable_maximum_page_length: this.maximum_page_lengthh,
    mutable_series_handler: this.series_handler,
    mutable_decision_method: this.decision_method,
    mutable_frequency: this.frequency,
    mutable_minimum_number_goodreads_r: this.minimum_number_goodreads_r,
    mutable_binding: this.binding,
      }
  },
  methods: {
      addGenre: function() {
          var key = Object.keys(this.genre_lookup).find(key => this.genre_lookup[key] === this.new_genre);
          this.mutable_genres.push(key);
          this.new_genre = '';
      },
      sendData: function() {
          var to_send = {};
          to_send['maximum_price'] = this.mutable_maximum_price;
          to_send['minimum_goodreads_rating'] = this.mutable_minimum_goodreads_rating;
          to_send['worst_allowable_condition'] = this.mutable_worst_allowable_condition;
          to_send['minimum_page_length'] = this.mutable_minimum_page_length;
          to_send['maximum_page_length'] = this.mutable_maximum_page_length;
          to_send['series_handler'] = this.mutable_series_handler;
          to_send['decision_method'] = this.mutable_decision_method;
          to_send['genres'] = this.mutable_genres;
          to_send['frequency'] = this.mutable_frequency;
          to_send['minimum_number_goodreads_r'] = this.mutable_minimum_number_goodreads_r;
          to_send['binding'] = this.mutable_binding;
          return to_send;
      },
  }
}
</script>

